<template>
  <div class="modal fade" id="myModal" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
    <div class="modal-dialog  modal-xl modal-dialog-scrollable" role="document">
      <div class="modal-content image-background-modal text-white">
        <div class="modal-header border-0 justify-content-end">
          <a class="modal-close pe-2" data-bs-dismiss="modal" aria-label="Close">&times;</a>
        </div>
        <div class="modal-body align-content-start">
          <component :is="this.modals[this.componentTitle]"/>
        </div>
        <div class="modal-footer border-0">
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {defineAsyncComponent} from 'vue';

const ModalSecondWar = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSecondWarDescription'));
const ModalSikorskiMajskiDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSikorskiMajskiDescription'));
const ModalGenAndersDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalGenAndersDescription'));
const ModalStartWarDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalStartWarDescription'));
const ModalSecondWarDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSecondWarDescription'));
const ModalSeventySeptemberDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSeventySeptemberDescription'));
const ModalSovietDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSovietDescription'));
const ModalZsrsDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalZsrsDescription'));
const ModalPolandInAfricaDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalPolandInAfricaDescription'));
const ModalCreateHistoryDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalCreateHistoryDescription'));
const ModalItalyAliantsDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalItalyAliantsDescription'));
const ModalBeforePolandDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalBeforePolandDescription'));
const ModalWarMonteCassinoDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalWarMonteCassinoDescription'));
const ModalPolandBannerDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalPolandBannerDescription'));
const ModalSecondCassinoDescription = defineAsyncComponent(() => import('@/components/Pages/SectionDescription/ModalSecondCassinoDescription'));

export default {
  props: ['componentName', 'modalId'],
  data() {
    return {
      componentTitle: '',
      modals: {
        'ModalSecondWar': ModalSecondWar,
        'ModalSecondWarDescription': ModalSecondWarDescription,
        'ModalSeventySeptemberDescription': ModalSeventySeptemberDescription,
        'ModalSovietDescription': ModalSovietDescription,
        'ModalZsrsDescription': ModalZsrsDescription,
        'ModalPolandInAfricaDescription': ModalPolandInAfricaDescription,
        'ModalCreateHistoryDescription': ModalCreateHistoryDescription,
        'ModalItalyAliantsDescription': ModalItalyAliantsDescription,
        'ModalBeforePolandDescription': ModalBeforePolandDescription,
        'ModalWarMonteCassinoDescription': ModalWarMonteCassinoDescription,
        'ModalSikorskiMajskiDescription': ModalSikorskiMajskiDescription,
        'ModalGenAndersDescription': ModalGenAndersDescription,
        'ModalStartWarDescription': ModalStartWarDescription,
        'ModalPolandBannerDescription': ModalPolandBannerDescription,
        'ModalSecondCassinoDescription': ModalSecondCassinoDescription
      }
    }
  },
  mounted() {
    let $vm = this;
    let $ = require('jquery');
    $('[data-bs-toggle="modal"]').on('click', function (e) {
      let $target = $(e.target);
      $vm.componentTitle = $target.data('myvalue');
    });
  }
}
</script>
